@use 'abstracts' as *;

.hero {
   @include m(home) {
      margin: 1rem 0 7rem;

      @include e(body) {
         grid-template-columns: 1fr;
         @include mq(min-width, tablet) {
            grid-template-columns: repeat(4, 1fr);

            @include e(img) {
               grid-column: 1 / -1;
               grid-row: 1 / -1;
            }

            @include e(txt) {
               grid-column: 1 / 4;
               grid-row: 1 / -1;
               align-self: flex-end;
               padding: 3.5rem 2rem 0 0;
               & .title {
                  max-width: 500px;
                  margin-bottom: 3rem;
               }

               @include mq(min-width, desktop) {
                  grid-column: 1 / 3;
               }
            }
         }
      }
   }

   @include m(details) {
      margin-bottom: var(--details-hero-ct);
   }
}
